.app-bar {
  padding: 8px 16px;
  background-color: white;
  display: grid;
  grid-template-columns: auto 1fr auto; /* Elemen pertama di kiri, elemen kedua di tengah, elemen ketiga di kanan */
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar__title {
  display: flex;
  align-items: center;
  text-align: center;
}
.app-bar__title h1 {
  text-align: left;
  margin: 0;
  justify-content: center;
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center; /* Pastikan tombol berada di tengah secara vertikal */
  justify-content: flex-end; /* Pastikan tombol berada di kanan dalam elemen ini */
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand h1 {
  color: #db0000;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
  margin: 0; /* Tambahkan margin: 0 untuk menghilangkan margin default h1 */
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  right: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: white;
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  right: 0;
}

.app-bar .app-bar__navigation ul {
  list-style: none; /* Menghilangkan bullet points dari list */
  padding: 0; /* Menghilangkan padding default dari ul */
  margin: 0; /* Menghilangkan margin default dari ul */
}

.app-bar .app-bar__navigation ul li {
  margin-bottom: 5px; /* Menambahkan margin-bottom pada setiap item list */
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: black;
  padding: 8px;
  width: 100%;
  box-sizing: border-box; /* Memastikan padding termasuk dalam width */
}

.app-bar .app-bar__navigation ul li a:hover {
  background-color: #b3c8cf;
  border-radius: 15%;
}

/* Hero Section */
.hero {
  width: 100%;
  height: 100vh;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center/cover no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  position: relative;
}

.hero__title {
  background: rgba(0, 0, 0, 0.5);
  padding: 20px;
  border-radius: 10px;
}

.cta-button {
  background-color: white;
  color: black;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.cta-button:hover {
  background-color: #0056b3;
}

/* New Spot Section */
