}

.main-content__new-spot h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 20px;
}

.main-content__new-spot__list-card {
  display: flex;
  gap: 20px;
  overflow-x: scroll;
  padding-bottom: 20px;
  margin-top: 20px auto;
  background-color: rebeccapurple;
  padding: 50px;
}

.destination-card {
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 10px;
  text-align: center;
  background-color: white;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Menambahkan properti transisi yang diinginkan */
  cursor: pointer;
}

.destination-card:hover {
  transform: scale(1.05); /* Efek zoom kecil saat di-hover */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Menambahkan bayangan untuk efek visual */
}

.destination-card img {
  width: 100%;
  border-radius: 10px;
}

.destination-card h3 {
  margin: 10px 0;
}

