oration: none;\n  color: white;\n  margin: auto 20px;\n  text-decoration: none;\n  list-style-type: none;\n  padding: 8px 16px;\n  transition: color 0.3s, background-color 0.3s;\n}\n\n.app-bar__navigation ul li a:hover {\n  color: black;\n  background-color: aquamarine;\n} */\n\n.app-bar {\n  padding: 8px 16px;\n  background-color: white;\n  display: grid;\n  grid-template-columns: auto 1fr auto; /* Elemen pertama di kiri, elemen kedua di tengah, elemen ketiga di kanan */\n  gap: 10px;\n  position: sticky;\n  top: 0;\n  z-index: 1000;\n  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\n}\n\n.app-bar__title {\n  display: flex;\n  align-items: center;\n  text-align: center;\n}\n.app-bar__title h1 {\n  text-align: left;\n  margin: 0;\n  justify-content: center;\n}\n\n.app-bar .app-bar__menu {\n  display: flex;\n  align-items: center; /* Pastikan tombol berada di tengah secara vertikal */\n  justify-content: flex-end; /* Pastikan tombol berada di kanan dalam elemen ini */\n}\n\n.app-bar .app-bar__menu button {\n  background-color: transparent;\n  border: none;\n  font-size: 18px;\n  padding: 8px;\n  cursor: pointer;\n}\n\n.app-bar .app-bar__brand {\n  display: flex;\n  align-items: center;\n}\n\n.app-bar .app-bar__brand h1 {\n  color: #db0000;\n  text-transform: uppercase;\n  font-size: 22px;\n  user-select: none;\n  margin: 0; /* Tambahkan margin: 0 untuk menghilangkan margin default h1 */\n}\n\n.app-bar .app-bar__navigation {\n  position: absolute;\n  top: 50px;\n  right: -180px;\n  width: 150px;\n  transition: all 0.3s;\n  padding: 8px;\n  background-color: white;\n  overflow: hidden;\n}\n\n.app-bar .app-bar__navigation.open {\n  right: 0;\n}\n\n.app-bar .app-bar__navigation ul {\n  list-style: none; /* Menghilangkan bullet points dari list */\n  padding: 0; /* Menghilangkan padding default dari ul */\n  margin: 0; /* Menghilangkan margin default dari ul */\n}\n\n.app-bar .app-bar__navigation ul li {\n  margin-bottom: 5px; /* Menambahkan margin-bottom pada setiap item list */\n}\n\n.app-bar .app-bar__navigation ul li a {\n  display: inline-block;\n  text-decoration: none;\n  color: black;\n  padding: 8px;\n  width: 100%;\n  box-sizing: border-box; /* Memastikan padding termasuk dalam width */\n}\n\n.app-bar .app-bar__navigation ul li a:hover {\n  background-color: #b3c8cf;\n  border-radius: 15%;\n}\n\n/* Hero Section */\n.hero {\n  width: 100%;\n  height: 100vh;\n  background: url(\"../public/desa-sade-1.jpeg\") center/cover no-repeat;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  text-align: center;\n  color: white;\n  position: relative;\n}\n\n.hero__title {\n  background: rgba(0, 0, 0, 0.5);\n  padding: 20px;\n  border-radius: 10px;\n}\n\n.cta-button {\n  background-color: white;\n  color: black;\n  padding: 10px 20px;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  margin-top: 20px;\n}\n\n.cta-button:hover {\n  background-color: #0056b3;\n}\n\n/* New Spot Section */\n.main-content__new-spot {\n  margin-top: 20px;\n}\n\n.main-content__new-spot h2 {\n  text-align: center;\n  font-size: 2rem;\n  margin-bottom: 20px;\n}\n\n.main-content__new-spot__list-card {\n  display: flex;\n  gap: 20px;\n  overflow-x: scroll;\n  padding-bottom: 20px;\n  margin-top: 20px auto;\n  background-color: rebeccapurple;\n  padding: 50px;\n}\n\n.destination-card {\n  width: 100%;\n  border: 1px solid #ddd;\n  border-radius: 10px;\n  padding: 10px;\n  text-align: center;\n  background-color: white;\n  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Menambahkan properti transisi yang diinginkan */\n  cursor: pointer;\n}\n\n.destination-card:hover {\n  transform: scale(1.05); /* Efek zoom kecil saat di-hover */\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Menambahkan bayangan untuk efek visual */\n}\n\n.destination-card img {\n  width: 100%;\n  border-radius: 10px;\n}\n\n.destination-card h3 {\n  margin: 10px 0;\n}\n\n/* Event Section\n.event {\n  margin-top: 40px;\n  text-align: center;\n}\n\n.event h2 {\n  font-size: 2rem;\n  margin-bottom: 20px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  gap: 10px;\n}\n\n.event__carousel {\n  position: center;\n  width: 40%;\n  margin: 0 auto;\n}\n\n.carousel__button {\n  position: absolute;\n  top: 50%;\n  transform: translateY(-50%);\n  background: none;\n  border: none;\n  font-size: 2rem;\n  cursor: pointer;\n  z-index: 1;\n  color: #007bff;\n}\n\n.carousel__button--left {\n  left: 0;\n}\n\n.carousel__button--right {\n  right: 0;\n}\n\n/* .carousel__track-container {\n  overflow: hidden;\n  border: 2px solid #007bff;\n  border-radius: 8px;\n} */\n/* \n.carousel__track {\n  display: flex;\n  flex-direction: row;\n  gap: 8px;\n  transition: transform 0.3s ease-in;\n  padding: 20px;\n}\n\n.carousel__slide {\n  min-width: 70%;\n  box-sizing: border-box;\n  border: 4px solid black;\n  gap: 5px;\n  border-radius: 10px;\n}\n\n.event__item {\n  display: flex;\n  align-items: center;\n  margin: 10px;\n  padding: 10px;\n  background: #e0f7fa;\n  border-radius: 8px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition: transform 0.3s;\n}\n\n.event__item:hover {\n  transform: translateY(-5px);\n}\n\n.event__date {\n  background: #00bcd4;\n  color: white;\n  font-size: 1.5rem;\n  font-weight: bold;\n  padding: 10px;\n  border-radius: 50%;\n  width: 50px;\n  height: 50px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  margin-right: 10px;\n}\n\n.event__description {\n  text-align: left;\n}\n\n.event__description h3 {\n  margin: 0;\n  font-size: 1.2rem;\n  color: #007bff;\n}\n\n.event__description p {\n  margin: 0;\n  color: #333;\n}\n\n.carousel__nav {\n  display: flex;\n  justify-content: center;\n  margin-top: 20px;\n}\n\n.carousel__indicator {\n  background: #ccc;\n  border: none;\n  width: 10px;\n  height: 10px;\n  border-radius: 50%;\n  margin: 0 5px;\n  cursor: pointer;\n}\n\n.carousel__indicator.current-slide {\n  background: #007bff;\n}\n\n.is-hidden {\n  